import React, { useEffect, useState } from "react";
import Chart from "react-apexcharts";
import { Empty } from "antd";

const VerticalBarChart = props => {
  const { categories, series } = props;
  const [chartData, setChartData] = useState();

  useEffect(() => {
    const data = {
      series: [
        {
          name: "",
          data: series ?? [],
        },
      ],
      options: {
        chart: {
          type: "bar",
          height: 450,
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            columnWidth: "30%",
          },
        },
        noData: {
          text: "No Data Found",
          align: "center",
          verticalAlign: "middle",
          offsetX: 0,
          offsetY: 0,
          style: {
            color: "#df4759",
            fontSize: "14px",
            fontFamily: undefined,
          },
        },
        dataLabels: {
          enabled: true,
        },
        xaxis: {
          categories: categories ?? [],
        },
        yaxis: {
          title: {
            text: "",
          },
          min: 0,
          max: Math.max(...series) + 100,
          tickAmount: 5,
        },
      },
    };
    setChartData(data);
  }, [categories, series]);

  return chartData ? (
    <Chart options={chartData.options} series={chartData.series} height={450} type="bar" />
  ) : (
    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} />
  );
};

export default VerticalBarChart;
